// export default Dashboard;

import AdminView from "./AdminView";
import MemberView from "./MemberView";

function Dashboard({ role }) {
  return (
    <div>
      <h2>Dashboard</h2>
      {role === "admin" ? <AdminView /> : <MemberView />}
    </div>
  );
}

export default Dashboard;

